html {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px; }

.wrapper {
  margin: 0 auto;
  max-width: 1400px; }

.navMenu {
  width: 255px;
  background-color: #001529;
  color: rgba(255, 255, 255, 0.65); }
  .navMenu__subMenu2 {
    background: #000c17;
    list-style-type: none; }
    .navMenu__subMenu2__text {
      min-height: 40px;
      display: flex;
      align-items: center;
      padding-left: 48px; }
      .navMenu__subMenu2__text:hover {
        color: #fff; }
  .navMenu__subMenu {
    padding: 0;
    width: 100%;
    margin-top: -200px;
    display: none;
    animation: openNav 1s; }
    .navMenu__subMenu--open {
      display: block;
      margin-top: 0; }
    .navMenu__subMenu__item {
      background: #000c17;
      list-style-type: none;
      max-width: 100%;
      padding: 0 16px 0 48px;
      min-height: 40px;
      display: flex;
      align-items: center;
      flex-wrap: wrap; }
      .navMenu__subMenu__item--padding {
        padding-left: 96px; }
      .navMenu__subMenu__item:hover {
        color: #fff; }
    .navMenu__subMenu__text {
      min-height: 40px;
      display: flex;
      align-items: center;
      max-width: 100%; }
      .navMenu__subMenu__text--navOne:before {
        content: " "; }
      .navMenu__subMenu__text--navOne:after {
        content: " "; }
  .navMenu__list {
    padding: 0; }
  .navMenu__icon {
    margin-right: 5px; }
  .navMenu__text {
    width: 100%;
    padding: 12px 16px 12px 24px;
    vertical-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    display: flex;
    align-items: center; }
    .navMenu__text:hover {
      color: #fff;
      transition: 0.5s; }
    .navMenu__text--active {
      background-color: #1890FF;
      color: #fff; }
  .navMenu__item {
    list-style-type: none;
    cursor: pointer;
    max-width: 100%;
    min-height: 52px;
    display: flex;
    align-items: center;
    flex-wrap: wrap; }
    .navMenu__item--arrowTop {
      position: relative; }
      .navMenu__item--arrowTop:after {
        content: '';
        background-image: url(../assets/img/svg/arrow.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        display: block;
        width: 6px;
        height: 6px;
        z-index: 20;
        top: 21px;
        right: 15px;
        opacity: 1;
        transform: rotate(-180deg); }
    .navMenu__item--arrowBot {
      position: relative; }
      .navMenu__item--arrowBot:before {
        content: '';
        background-image: url(../assets/img/svg/arrow.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        display: block;
        width: 6px;
        height: 6px;
        z-index: 20;
        top: 21px;
        right: 15px; }

@keyframes openNav {
  0% {
    opacity: 0;
    margin-top: -20px; }
  100% {
    opacity: 1;
    margin-top: 0px; }
  0% {
    opacity: 0;
    margin-top: -20px; } }

@media (max-width: 576px) {
  .navMenu {
    max-width: 80px;
    position: relative; }
    .navMenu__option {
      position: relative; }
      .navMenu__option:hover .navMenu__optionText {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        right: -75px;
        top: 11px;
        width: 70px;
        height: 30px;
        background-color: #808080; }
    .navMenu__subMenu2 {
      background-color: #001529;
      position: relative; }
      .navMenu__subMenu2:after {
        content: '';
        background-image: url(../assets/img/svg/arrow.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        display: block;
        width: 6px;
        height: 6px;
        z-index: 20;
        top: 21px;
        right: 15px;
        transform: rotate(90deg); }
      .navMenu__subMenu2__text {
        padding-left: 13px; }
    .navMenu__item--navOne:hover #navSubMenu1 {
      display: block; }
    .navMenu__item--navTwo:hover #navSubMenu2 {
      display: block;
      top: 200px; }
    .navMenu__item--navThree:hover #navSubMenu3 {
      display: block;
      top: -40px; }
    .navMenu__text {
      display: flex;
      justify-content: center;
      padding: 13px 30px; }
    .navMenu__subMenu {
      margin-top: 0;
      position: absolute;
      top: 0;
      right: -155px;
      width: 150px; }
      .navMenu__subMenu__item {
        padding-left: 10px;
        background-color: #001529; }
        .navMenu__subMenu__item:hover {
          background-color: #1890FF; }
        .navMenu__subMenu__item--active {
          background-color: #1890FF; }
      .navMenu__subMenu--open {
        position: absolute;
        top: 0;
        right: -155px;
        width: 150px; }
    .navMenu__optionText {
      display: none;
      position: relative; }
      .navMenu__optionText:before {
        content: '';
        background-image: url(../assets/img/svg/arrowDown.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        display: block;
        width: 6px;
        height: 6px;
        top: 13px;
        right: 68px;
        transform: rotate(90deg); }
    .navMenu__item--arrowTop:after {
      display: none; }
    .navMenu__item--arrowBot:before {
      display: none; } }
